body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

#root{
  background-color: #c6f6c6;
}

html{
  background-color: #c6f6c6;
}

.container-fluid{
  background: url(./images/image-title2.jpg) no-repeat center center fixed;
  background-size: cover;
  height: 100%;
  padding: 0px !important;
  font-family: 'Dosis', sans-serif;
  padding-bottom: 15px !important;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@font-face {
  font-family: 'Dosis';
  src: local('Dosis'), url(./fonts/dosis/Dosis-VariableFont_wght.ttf) format('truetype');
}

h1{
  font-family: 'Dosis', sans-serif;
}

h2.text-center{
  margin: 20px 0px;
  padding: 20px;
  color:#3a8d3a;
  vertical-align: middle;
  background-color: #e3fae3;
  border-radius: 0.25rem;
}

form{
  align-self: center;
}

form, .chart, .error{
  margin: 0rem 2rem;
}

.chart{
  margin-top: 10px;
}

.chart-help{
  background-color: #e3fae3;
  border-radius: 0.25rem;
  padding: 23px;
  margin-bottom: 20px;
}

#chartdiv{
  background-color: #e3fae3;
  border-radius: 0.25rem;
}

.form-box{
  display: flex;
  background-color: #3a8d3a;
  color: #fff;
  padding-top: 2rem;
  padding-bottom: 2rem;
}

.jumbotron{
  padding: 2rem 1rem !important;
  margin-bottom: 2rem !important;
  border-radius: 0rem !important;
}

label,option{
  font-size: 1.5rem;
}

.form-group{
  margin-top: 1.5rem;
}

.chart-title{
  border-radius: 0.25rem;
}

.chart-title-detail{
  color:#000;
}

.error{
  height: 30vh;
  font-size: 1.5rem;
}

.bullet-cash{
  list-style-image: url(./images/cash.svg);
  list-style-position: outside;
}

.bullet-graph{
  list-style-image: url(./images/graph-up.svg);
  list-style-position: outside;
}

.bullet-calendar{
  list-style-image: url(./images/calendar-date-fill.svg);
  list-style-position: outside;
}

.bullet-click{
  list-style-image: url(./images/hand-index-thumb.svg);
  list-style-position: outside;
}

li span{
  margin-left: 30px;
  display: block;
  margin-top: 5px;
}

.header{
  background-image: url(./images/header2.jpg);
  background-size: cover;
  color:#fff;
}

.text{
  z-index:4;
  line-height: 2em;
  font-size: 1.5rem;
  background-color: #c6f6c6aa !important;
  margin: 50px 0px;
}

.underlined{
  text-decoration: underline;
}

.bold{
  display: inline;
  margin-left: 0px;
  font-weight: bold;
}

.error{
  padding: 0px 15px;
}

.error-message{
  height:60px;
  display: flex;
  align-items: center;
}

@media(max-width: 768px){
  .media-width{
    width: 768px !important;
  }
}